import {DialogContainer} from 'dashboard/dialog'
import * as React from 'react'
import {render} from 'react-dom'
import {App} from './app'

require('./style/definitions.scss')
require('dashboard/style/defaults.global.scss')
require('react-mdl/extra/material.css')
require('react-mdl/extra/material.js')

function renderApp(App) {
  const app = document.getElementById('app')
  const dialog = document.getElementById('dialog')

  if (module.hot) {
    const RedBox = require('redbox-react')
    try {
      render(<App />, app)
    } catch (error) {
      error.stack = error.stack
        .replace(/http:\/\/localhost:3000\/webpack:/g, __ROOT_PATH__)
        .replace(/~/, 'node_modules')
        .replace(/\?[0-9a-z]+:/g, ':')
      render(<RedBox error={error} editorScheme='code' />, app)
    }
  } else {
    render(<App />, app)
  }
  render(<DialogContainer />, dialog)
}

if (window.document) {
  window.React = React
  renderApp(App)
}

if (module.hot) {
  module.hot.accept('./app', () => {
    const UpdatedApp = require('./app').App
    setTimeout(() => renderApp(UpdatedApp))
  })
}
